<template>
	<div class="auth_page text-center">
		<b-container>
			<b-navbar class="app-navbar p-0">
				<b-navbar-brand to="/">
					<Logo />
				</b-navbar-brand>
			</b-navbar>
			<b-card no-body class="auth_card border-0 mx-auto">
				<h4 class="mb-5">{{ $t("login") }}</h4>
				<div class="auth_btns">
					<a class="btn_auth" v-for="(btn, i) in authBtns" :key="i" :href="btn.redirect">
						<img :src="btn.image" :alt="btn.text" class="mr-3" width="28px" />
						<h6 class="mb-0">{{ btn.text }}</h6>
					</a>
				</div>
				<div class="auth_card_footer text-decoration-none fs-sm">
					<div class="my-4">
						<router-link to="/login/local" class="text-decoration-none">{{ $t("orLoginWithEmail") }}</router-link>
					</div>
					<div class="my-4">
						<span class="text-muted"> {{ $t("donNotHanveAnAccount") }}&nbsp;</span>
						<router-link to="/signup" class="text-decoration-none">{{ $t("signupForFree") }}</router-link>
					</div>
				</div>
			</b-card>
		</b-container>
	</div>
</template>

<script>
	import Logo from "@/components/layouts/Logo";

	export default {
		components: { Logo },

		async mounted() {
			await this.$store.dispatch("getGoogleRedirect");
		},

		computed: {
			authBtns() {
				return this.$store.state.auth.authBtns.map((b) => {
					if (b.provider == "google") {
						b.text = this.$t("loginWithGoogle");
					}
					return b;
				});
			},
		},
	};
</script>
